import { useState } from "react";
import { Link } from "react-router-dom";
// import Headroom from "react-headroom";
import logo from "../../assets/images/header/logo.svg";
import arrow from "../../assets/images/header/arrowicon.svg";
// import menu from "../../assets/images/header/menu.svg";
import "./header.css";

const WhatNavList = [
  {
    name: "whatNav",
    nav: "Immigrate",
    url: "/",
    subNav: [
      {
        subTitle: "Express Entry",
        url: "/",
        finalTitle: [
          "A fast-track immigration program for permanent residency.",
        ],
      },
      {
        subTitle: "Provincial Nominee Programs",
        url: "/",
        finalTitle: [],
      },
      {
        subTitle: "Atlantic Immigration Program",
        url: "/",
        finalTitle: [],
      },
    ],
  },
  {
    name: "whatNav",
    nav: "Work & Travel",
    url: "/",
    subNav: [],
  },
  {
    name: "whatNav",
    nav: "Study",
    url: "/",
    subNav: [],
  },
  {
    name: "whatNav",
    nav: "Business",
    url: "/",
    subNav: [],
  },
  {
    name: "whatNav",
    nav: "Sponsorship",
    url: "/",
    subNav: [],
  },
];

// console.log("WhatNavList>>", WhatNavList);
const Header = () => {
  const [iconView, setIconView] = useState(false);
  // const [hoverMenu, setHoverMenu] = useState("");

  const hoverMenu = "";

  // const handleMenuClick = (e) => {
  //   setHoverMenu(e);
  //   console.log(e);
  // };
  // console.log("hoverMenu>>", hoverMenu);

  // const handleEvent = (a) => {
  //   console.log(a);
  //   setHoverMenu(a);
  // };

  // console.log("hoverMenu>>", hoverMenu);

  return (
    // <Headroom>
    <div className="header">
      <div className="container">
        <div className="header-content">
          <div className="logo">
            <Link to="/">
              <img src={logo} alt="logo" />
            </Link>
          </div>
          <div className="header-menu">
            <div
              className="mega-menu"
              // onMouseLeave={() => handleEvent("")}
              // onMouseOver={() => handleEvent("whatNav")}
            >
              <div className="header-menu-list">
                <span>What</span>
                <img
                  style={{
                    rotate: `${hoverMenu === "whatNav" ? "180deg" : "0deg"}`,
                    transition: "all 0.3s",
                  }}
                  src={arrow}
                  alt="arrow"
                />
              </div>
              {hoverMenu === "whatNav" ? (
                <div
                  className="menu-list-wrapper what-nav-wrapper"
                  // onMouseLeave={() => handleEvent("")}
                >
                  <div className="container">
                    <ul className="menu-list-container">
                      {WhatNavList.map((e, i) => (
                        <li key={i}>
                          <Link className="menu-list-item" to={e?.url}>
                            {e?.nav}
                          </Link>
                          <ul className="menu-list-category">
                            {e?.subNav.map((el) => (
                              <li>
                                <Link className="menu-list-item" to={el?.url}>
                                  {el?.subTitle}
                                </Link>
                                <ul className="menu-list-subcategory">
                                  {el?.finalTitle.map((elem) => (
                                    <li>
                                      <span className="menu-list-item">
                                        {elem}
                                      </span>
                                    </li>
                                  ))}
                                </ul>
                              </li>
                            ))}
                          </ul>
                        </li>
                      ))}
                      {/* <li>
                          <Link className="menu-list-item" to="/">
                            Work & Travel
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Study
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Business
                          </Link>
                        </li>
                        <li>
                          <Link className="menu-list-item" to="/">
                            Sponsorship
                          </Link>
                        </li> */}
                    </ul>
                  </div>
                </div>
              ) : (
                ""
              )}
            </div>
            <div
              className="mega-menu"
              // onMouseLeave={() => handleEvent("")}
              // onMouseOver={() => handleEvent("whyNav")}
            >
              <div className="header-menu-list">
                <span>Why</span>
                <img
                  style={{
                    rotate: `${hoverMenu === "whyNav" ? "180deg" : "0deg"}`,
                    transition: "all 0.3s",
                  }}
                  src={arrow}
                  alt="arrow"
                />
              </div>
              {hoverMenu === "whyNav" ? (
                <div
                  className="menu-list-wrapper  why-nav-wrapper"
                  // onMouseLeave={() => handleEvent("")}
                >
                  <div className="container">
                    <ul className="menu-list-container">
                      <li>
                        <Link className="menu-list-item" to="/">
                          Reviews
                        </Link>
                      </li>
                      <li>
                        <Link className="menu-list-item" to="/">
                          RCIC
                        </Link>
                      </li>
                    </ul>
                  </div>
                </div>
              ) : (
                ""
              )}
            </div>
            <div
              className="mega-menu"
              // onMouseLeave={() => handleEvent("")}
              // onMouseOver={() => handleEvent("howNav")}
            >
              <div className="header-menu-list">
                <span>How</span>
                <img
                  style={{
                    rotate: `${hoverMenu === "howNav" ? "180deg" : "0deg"}`,
                    transition: "all 0.3s",
                  }}
                  src={arrow}
                  alt="arrow"
                />
              </div>
              {hoverMenu === "howNav" ? (
                <div
                  className="menu-list-wrapper how-nav-wrapper"
                  // onMouseLeave={() => handleEvent("")}
                >
                  <div className="container">
                    <ul className="menu-list-container">
                      <li>
                        <Link className="menu-list-item" to="/">
                          Education center
                        </Link>
                      </li>
                      <li>
                        <Link className="menu-list-item" to="/">
                          FAQs
                        </Link>
                      </li>
                      <li>
                        <Link className="menu-list-item" to="/">
                          Free assessment
                        </Link>
                      </li>
                    </ul>
                  </div>
                </div>
              ) : (
                ""
              )}
            </div>
          </div>
          <div
            className="toggle-icon menu-toggle-wrapper"
            onClick={() => setIconView(!iconView)}
            role="presentation"
          >
            {iconView ? <CloseIcon /> : <MenuIcon />}
          </div>
        </div>
      </div>
      {iconView && (
        <div className="menu-list-wrapper">
          <div className="container">
            <ul className="menu-list-container">
              <li>
                <Link className="menu-list-item" to="/">
                  About
                </Link>
              </li>
              <li>
                <Link className="menu-list-item" to="/">
                  Blog
                </Link>
              </li>
              <li>
                <Link className="menu-list-item" to="/">
                  Contact Us
                </Link>
              </li>
              <li>
                <span className="menu-list-item">
                  Personal Portal / Registration
                </span>
              </li>
              <li>
                <span className="menu-list-item">Search</span>
                <span className="menu-list-item">En/Fr</span>
              </li>
            </ul>
          </div>
        </div>
      )}
    </div>
    // </Headroom>
  );
};

export default Header;

const MenuIcon = () => {
  return (
    <svg
      width="34"
      height="27"
      viewBox="0 0 34 27"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <line x1="6" y1="8" x2="28" y2="8" stroke="#252D43" strokeWidth="2" />
      <line x1="6" y1="13" x2="28" y2="13" stroke="#252D43" strokeWidth="2" />
      <line x1="6" y1="18" x2="28" y2="18" stroke="#252D43" strokeWidth="2" />
    </svg>
  );
};

const CloseIcon = () => {
  return (
    <svg
      width="34"
      height="27"
      viewBox="0 0 34 27"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <path
        d="M24 7.41L22.59 6L17 11.59L11.41 6L10 7.41L15.59 13L10 18.59L11.41 20L17 14.41L22.59 20L24 18.59L18.41 13L24 7.41Z"
        fill="black"
      />
    </svg>
  );
};
