import Container from "react-bootstrap/Container";
import Slider from "react-slick";

import "./review.css";

const ReviewData = [
  {
    id: 1,
    user: "Margot Babin",
    address: "21, France",
    desc: "J'aimerais partager mon incroyable exprience avec Canada District dans l'obtention de mon visa de travailleur de vacances. Tout tait simple et efficace, ils se sont occups de toutes les tapes ncessaires et m'ont aid avec de trs bons conseils tout au long du processus. Je suis tellement heureux d'avoir choisi Canada District pour mon voyage au Canada et je le recommande vivement  tous ceux qui recherchent un excellent service!",
  },
  {
    id: 2,
    user: "Emmet Mller",
    address: "32, Germany",
    desc: "I want to share my positive experience with Canada District who recently helped me get my student visa. Their team was exceptional in helping me understand all the details of the process and guiding me every step of the way. Their professionalism made the process very smooth and stress-free. After graduation Im hoping to get a post-graduate visa, gain more work experience and eventually settle in Canada. And I am definitely going to use Canada District's services",
  },
  {
    id: 3,
    user: "Johannes De Beer",
    address: "36, South Africa",
    desc: "Canada District helped me obtain my Express Entry work visa so I could continue my work as a financial advisor in Canada. The process was difficult and long and we had to resubmit my profile but we were finally successful. The team was knowledgeable and efficient in handling my case. They offered valuable support and we navigated this complex process together. I highly recommend Canada District to anyone looking for a reliable and trustworthy partner in securing a work visa in Canada.",
  },
  {
    id: 4,
    user: "Ava Thomas",
    address: "41, UK",
    desc: "The job search service provided by Canada District was simply brilliant, they went the extra mile in finding me the perfect job match. The team was knowledgeable, approachable, and always on hand to answer any queries I had. They made the entire process of finding a job and getting my visa an absolute breeze. I am ever so grateful to Canada District for helping me kickstart my new life in Canada and I wouldn't hesitate in recommending their services to anyone looking for work in this fantastic country.",
  },
  {
    id: 5,
    user: "Benjamin Roberts",
    address: "28, Australia",
    desc: "Canada District were bloody brillian to work with! Super friendly and professional team. But what really set them apart was their Working Holiday package. I had the time of my life in Canada, exploring new spots and meeting a top bunch of people, all while earning some cash. I highly recommend Canada District's Working Holiday package and their services",
  },
];

const ReviewSection = () => {
  const settings = {
    dots: false,
    centerMode: true,
    className: "center",
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    // slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 991,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 767,
        settings: {
          infinite: true,
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  return (
    <div className='review-main'>
      <div className='review-wrapper'>
        <Container>
          <div className='review-head'>
            <div className='page-title'>
              <div className='title-divider' />
              <span>Our Reviews</span>
            </div>
            <p>
              Weve helped over 3,000 immigrants make their dreams come true.
              Our customers have great things to share from their experiences in
              Canada.
            </p>
          </div>
        </Container>
        <div className='review-slider'>
          <Slider {...settings} swipeToSlide={true} focusOnSelect={true}>
            {ReviewData.map((e) => (
              <div key={e.id}>
                <div className='review-slider-content'>
                  <div className='review-slide'>
                    <div className='review-slide-head'>
                      <div className='review-profile'>
                        <div className='review-profile-top'>
                          <UserIcon />
                          <h6>{e?.user}</h6>
                        </div>
                        <div className='review-profile-desc'>
                          <span>{e?.address}</span>
                        </div>
                      </div>
                      <div className='review-stars'>
                        <StarIcon />
                        <StarIcon />
                        <StarIcon />
                        <StarIcon />
                        <StarIcon />
                      </div>
                    </div>
                    <div className='review-slider-contents'>
                      <p>{e?.desc}</p>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </Slider>
        </div>
      </div>
    </div>
  );
};

export default ReviewSection;

