import React from "react";
import box1 from "../../assets/images/ournews/box1.png";
import box2 from "../../assets/images/ournews/box2.png";
import box3 from "../../assets/images/ournews/box3.png";
import date from "../../assets/images/ournews/Date.svg";
// import arrow from "../../assets/images/ournews/arrow.svg";
import Slider from "react-slick";
import "./ournews.css";
import { Link } from "react-router-dom";
import { Button } from "react-bootstrap";

const OurNews = () => {
  const settings = {
    className: "center",
    centerMode: true,
    infinite: true,
    // centerPadding: "60px",
    slidesToShow: 3,
    slidesToScroll: 1,
    responsive: [
      {
        breakpoint: 991,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 767,
        settings: {
          infinite: false,
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };
  const data = [
    {
      img: box1,
      dateimg: date,
      time: "March 2023",
      title: "Master of Business Administration: Why Study MBA in Canada",
      read: "Read",
    },
    {
      img: box2,
      dateimg: date,
      time: "March 2023",
      title: "Master of Business Administration: Why Study MBA in Canada",
      // readimg: arrow,
      read: "Read",
    },
    {
      img: box3,
      dateimg: date,
      time: "March 2023",
      title: "Master of Business Administration: Why Study MBA in Canada",
      read: "Read",
    },
  ];
  return (
    <div className="ournews">
      <div className="container">
        <div className="ournews-wrapper">
          <div className="page-title">
            <div className="title-divider" />
            <span>Our News</span>
          </div>
          <div className="ournews-content">
            {data.map((data, i) => (
              <div className="ournews-box" key={i}>
                <img className="ournews-box-img" src={data.img} alt="box" />
                <div className="ournews-box-body">
                  <div className="ournews-box-date">
                    <img src={data.dateimg} alt="date" />
                    <span>{data.time}</span>
                  </div>
                  <p>{data.title}</p>
                  <Link to="/">{data.read}</Link>
                </div>
              </div>
            ))}
          </div>
          <div className="ournews-content-slider">
            <Slider {...settings} swipeToSlide={true} focusOnSelect={true}>
              {data.map((data, i) => (
                <div key={i}>
                  <div className="ournews-content-slider-grid">
                    <div className="ournews-box">
                      <img
                        className="ournews-box-img"
                        src={data?.img}
                        alt="box"
                      />
                      <div className="ournews-box-body">
                        <div className="ournews-box-date">
                          <img src={data?.dateimg} alt="date" />
                          <span>{data?.time}</span>
                        </div>
                        <p>{data?.title}</p>
                        <Link to="/">{data?.read}</Link>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </Slider>
          </div>
          <div className="ournews-btn">
            <Button className="cta-btn">View All Articles</Button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default OurNews;
