import { useRef, useState } from "react";
import Button from "react-bootstrap/Button";
import Container from "react-bootstrap/Container";
import Slider from "react-slick";

import "./herobanner.css";

const BannerContent = [
  {
    id: 1,
    bgImg: "./images/bg1.png",
    subTitle: "Canada District - Canadian Immigration Services",
    mainTitle: "Express Entry",
    mainPara: "A fast-track immigration program for permanent residency.",
  },
  {
    id: 2,
    bgImg: "./images/bg2.png",
    subTitle: "Canada District - Canadian Immigration Services",
    mainTitle: "Study",
    mainPara: "A fast-track immigration program for permanent residency.",
  },
  {
    id: 3,
    bgImg: "./images/bg3.png",
    subTitle: "Canada District - Canadian Immigration Services",
    mainTitle: "Business",
    mainPara: "A fast-track immigration program for permanent residency.",
  },
  {
    id: 4,
    bgImg: "./images/bg4.png",
    subTitle: "Canada District - Canadian Immigration Services",
    mainTitle: "PNP",
    mainPara: "A fast-track immigration program for permanent residency.",
  },
  {
    id: 5,
    bgImg: "./images/bg5.png",
    subTitle: "Canada District - Canadian Immigration Services",
    mainTitle: "Sponsorship",
    mainPara: "A fast-track immigration program for permanent residency.",
  },
];

const HeroBanner = () => {
  const [indexValue, setIndexValue] = useState(1);
  const SliderObj = BannerContent.filter((e) => e.id === indexValue);
  const SliderContent = SliderObj[0];
  return (
    <div className="main-banner">
      <div
        className="hero-bg-img"
        style={{ backgroundImage: `url(${SliderContent?.bgImg})` }}
      >
        <div className="hero-bg-content">
          <Container>
            <div className="hero-content-wrapper">
              <div className="hero-sub-title">
                <span>{SliderContent?.subTitle}</span>
                <div className="divider-bar" />
              </div>
              <div className="hero-main-content">
                <h1>{SliderContent?.mainTitle}</h1>
                <p>{SliderContent?.mainPara}</p>
              </div>
              <div className="hero-banner-btn">
                <Button className="cta-btn">Book a Free Consultation</Button>
                <span>Read More</span>
              </div>
            </div>
          </Container>
        </div>
      </div>
      <BannerSlider setIndexValue={setIndexValue} />
      {/* <span className="slider-count">{indexValue}/5</span> */}
    </div>
  );
};

export default HeroBanner;

const BannerSlider = ({ setIndexValue }) => {
  const slider = useRef(null);
  const settings = {
    dots: false,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    afterChange: (e) => setIndexValue(e + 1),
    responsive: [
      {
        breakpoint: 991,
        settings: {
          slidesToShow: 2,
        },
      },
      {
        breakpoint: 767,
        settings: {
          slidesToShow: 2,
        },
      },
    ],
  };
  const handleNext = () => {
    slider.current.slickNext();
  };
  const handlePrevious = () => {
    slider.current.slickPrev();
  };
  return (
    <div className="hero-main-slider">
      <div className="hero-slider-btn">
        <button className="button" onClick={handlePrevious}>
          <PrevBtnIcon />
        </button>
        <button className="button" onClick={handleNext}>
          <NextBtnIcon />
        </button>
      </div>
      <div className="slider-wrapper">
        <Slider
          ref={slider}
          {...settings}
          swipeToSlide={true}
          focusOnSelect={true}
        >
          <div className="hero-single-slide" key={1}>
            <img src="./images/sliderBg1.png" alt="" />
            <div className="hero-slider-content">
              <span>Express Entry</span>
              <div className="slider-content-divider" />
            </div>
          </div>
          <div className="hero-single-slide" key={2}>
            <img src="./images/sliderBg2.png" alt="" />
            <div className="hero-slider-content">
              <span>Study</span>
              <div className="slider-content-divider" />
            </div>
          </div>
          <div className="hero-single-slide" key={3}>
            <img src="./images/sliderBg3.png" alt="" />
            <div className="hero-slider-content">
              <span>Business</span>
              <div className="slider-content-divider" />
            </div>
          </div>
          <div className="hero-single-slide" key={4}>
            <img src="./images/sliderBg4.png" alt="" />
            <div className="hero-slider-content">
              <span>PNP</span>
              <div className="slider-content-divider" />
            </div>
          </div>
          <div className="hero-single-slide" key={5}>
            <img src="./images/sliderBg5.png" alt="" />
            <div className="hero-slider-content">
              <span>Sponsorship</span>
              <div className="slider-content-divider" />
            </div>
          </div>
        </Slider>
      </div>
    </div>
  );
};

const PrevBtnIcon = () => {
  return (
    <svg
      width="39"
      height="39"
      viewBox="0 0 39 39"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle
        cx="19.5"
        cy="19.5"
        r="19"
        transform="rotate(-180 19.5 19.5)"
        stroke="#252D43"
      />
      <path
        d="M16.35 12L17.8305 13.5107L13.0215 18.4286L30 18.4286L30 20.5714L13.0215 20.5714L17.841 25.4893L16.35 27L9 19.5L16.35 12Z"
        fill="#252D43"
      />
    </svg>
  );
};

const NextBtnIcon = () => {
  return (
    <svg
      width="39"
      height="39"
      viewBox="0 0 39 39"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx="19.5" cy="19.5" r="19" stroke="#252D43" />
      <path
        d="M22.65 27L21.1695 25.4893L25.9785 20.5714L9 20.5714L9 18.4286L25.9785 18.4286L21.159 13.5107L22.65 12L30 19.5L22.65 27Z"
        fill="#252D43"
      />
    </svg>
  );
};
